<template>
  <div class = "childMainWindow">
    <!--    面包屑导航区域-->
    <el-breadcrumb separator-class="el-icon-arrow-right" style="margin-bottom: 15px">
      <el-breadcrumb-item >首页</el-breadcrumb-item>
      <el-breadcrumb-item>交易记录</el-breadcrumb-item>
    </el-breadcrumb>
    <!--    主体页面-->
    <div class="mainTransactionRecord">
      <el-card class="searchCard" shadow="never">
        <el-form :model="queryForm" class="el-form" ref="queryForm" inline>
          <el-form-item label="订单编号：" prop="orderNo" >
            <el-input v-model="queryForm.orderNo" placeholder="订单编号"></el-input>
          </el-form-item>
          <el-form-item label="商品编号：" prop="productNo">
            <el-input v-model="queryForm.productNo" placeholder="商品编号"></el-input>
          </el-form-item>
          <el-form-item label="收货人：" prop="user">
            <el-input v-model="queryForm.user" placeholder="收货人姓名/手机号"></el-input>
          </el-form-item>
          <el-form-item label="订单状态：" prop="orderState">
            <el-select v-model="queryForm.orderState" filterable placeholder="全部" clearable>
              <el-option v-for="item in orderStates" :key="item" :label="item" :value="item"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item >
            <el-button class="el-button-search" type="primary" @click="queryOrder()" style="margin-left: 40%">查询</el-button>
          </el-form-item>
        </el-form>
      </el-card>
      <el-card class="mainCard">
        <!--        帖子内容-->
        <el-table :data="thingInfo" style="width: 100%;font-size: 14px;height: 100%" fit highlight-current-row  class="userTable" v-loading="loading" >
          <!--          用户基本信息-->
          <el-table-column prop="thing_id" label="#商品id号" width="110" type="index"></el-table-column>
          <el-table-column prop="username" label="用户名" width="110"></el-table-column>
          <el-table-column prop="thing_name" label="商品名" width="110"></el-table-column>
          <el-table-column prop="type" label=" 商品类别" width="110"></el-table-column>
          <el-table-column prop="price" label="价格" width="100"></el-table-column>
          <el-table-column prop="phone" label="电话" width="130"></el-table-column>
          <el-table-column prop="qq" label="QQ" width="120"></el-table-column>
          <el-table-column prop="die_time" label="到期时间" width="160"></el-table-column>
          <!--          操作-->
          <el-table-column  label="操作" width="120" header-align="center">
            <template slot-scope="scope"  >
              <el-tooltip  effect="dark" content="商品备注" placement="top-end" :enterable="false">
                <!--              查看帖子内容按钮-->
                <el-button type="primary" icon="el-icon-c-scale-to-original" size="mini"  @click="showMainContent(scope.row.remark)"></el-button>
              </el-tooltip>
              <!--              删除按钮-->
              <el-button type="danger" icon="el-icon-delete" size="mini"  @click="deleteDialog(scope.row.thing_id)" ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
      <!--        分页-->
      <div class="block">
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="queryInfo.pageNum"
            :page-sizes="queryInfo.pageSizes"
            :page-size="queryInfo.pageSize"
            :current-page="queryInfo.pageIndex"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalMes"
            style="margin-left: 350px">
        </el-pagination>
      </div>
    </div>
    <!--    显示商品主体信息的对话框-->
    <el-dialog
        :visible.sync="seeThingDialogVisible"
        title="商品备注"
        width="30%">
      <span>{{thingRemark}}</span>
      <span slot="footer" class="dialog-footer">
      <el-button @click="seeThingDialogVisible = false">关 闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  // 变量
  data () {
    return {
      // 搜索商品
      searchThing: '',
      // 页面加载
      loading: false,
      // 已选物品类别,商品关键字，页数，一页个数
      queryInfo: {
        pageNum: 1,
        orderTotal: 0, // 数据的总条数
        pageIndex: 1, //当前页面显示的页码
        pageSize: 10, // 每页显示的数据条数
        pageSizes:[5,10,30,60,100,200,400],
      },
      //搜索表单
      queryForm:{
        orderNo:'',   /*订单编号*/
        productNo:'', /*商品编号*/
        user: '',   /*收货人*/
        orderState:'',/*订单状态*/
      },
      // 总信息条数
      totalMes: 100,
      // 商品其他信息
      thingInfo: [
      ],
      orderStates:['待付款','待发货','已发货','已收货','已评价','开通成功'],
      // 商品备注显示
      thingRemark: '',
      // 控制编辑商品对话框控件
      editThingDialogVisible: false,
      // 控制删除商品对话框控件
      deleteThingDialogVisible: false,
      // 控制打开商品主体内容的控件
      seeThingDialogVisible: false
    }
  },
  // 生命周期函数
  created () {
    // 获取全部商品
    this.getItemMes()
    // 获取全部商品数量
    this.getItemNumber()
  },
  // 函数方法
  methods: {
    // 根据queryInfo获得帖子列表的函数,获得商品的各个属性（待写） !!!!!
    async getItemMes () {
      // 页面进入待刷新状态
      this.loading = true
      // 传递查询参数
      const formData = new FormData()
      formData.append('pagenum', this.queryInfo.pageNum)
      formData.append('keywords', this.searchThing)
      formData.append('type', this.queryInfo.itemCategory)
      formData.append('inschool', '0')
      const token = window.sessionStorage.getItem('token')

      const { data: res } = await this.$axios.post('/thing_get', formData,
          {
            headers: {
              accesstoken: token
            }
          })
      if (!res) {
        this.$message.error('获取信息失败')
      }
      // 得到商品数据
      this.thingInfo = res
      console.log(res)
      this.$forceUpdate()
      // 页面刷新成功
      this.loading = false
      this.$message.success('商品信息刷新成功')
    },
    // 获取商品总数
    async getItemNumber () {
      // 传递查询参数
      const formData = new FormData()
      formData.append('keywords', this.searchThing)
      formData.append('type', this.queryInfo.itemCategory)
      formData.append('inschool', '0')

      const token = window.sessionStorage.getItem('token')

      const { data: res } = await this.$axios.post('/thing_get_number', formData,
          {
            headers: {
              accesstoken: token
            }
          })
      if (!res && res !== 0) {
        this.totalMes = 100
        this.$message.error('获取商品总数失败')
      }
      // 得到商品数据
      this.totalMes = res
    },
    // 监听页数改变
    async handleSizeChange () {
      console.log(this.queryInfo.pageNum)
      // 刷新商品信息
      await this.getItemNumber()
      await this.getItemMes()
    },
    // 显示商品主要内容
    showMainContent (remake) {
      this.thingRemark = remake
      this.seeThingDialogVisible = true
    },
    // 显示删除商品对话框
    deleteDialog (thing_id) {
      console.log(thing_id)
      this.$confirm('是否永久删除该商品, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteThing(thing_id)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 删除商品
    async deleteThing (thing_id) {
      // 传递查询参数
      const token = window.sessionStorage.getItem('token')
      const deleteForm = new FormData()
      deleteForm.append('id', thing_id)
      console.log(thing_id)
      const { data: res } = await this.$axios.post('/thing_reset', deleteForm,
          {
            headers: {
              accesstoken: token
            }
          })
      console.log(res)
      if (res !== 1) {
        return this.$message.error('商品删除失败')
      }
      this.$message.success('商品删除成功')
      // 获得商品属性
      await this.getItemNumber()
      await this.getItemMes()
    },
    //查询按钮操作
    queryOrder(){

    }
  }
}
</script>

<style land="less" scoped>
.childMainWindow{
  height: 100%;
  width: 100%;
}
.mainTransactionRecord{
  height: 100%;
}
.mainCard{
  height: 70%;
}
.searchCard{
  height: 12%;
  margin-bottom: 10px;
}
.block{
  bottom: 10px;
}

</style>
