<template>
  <div class="register-container">
    <div>
      <!-- 背景图片区域 -->
      <div class="backgroundPicture">
    <el-form
      ref="registerForm"
      label-width="150px"
      class="register-form"
      :model="registerForm"
      :rules="registerRules"
    >
      <div>
        <img class="register_in_title" src="../assets/image/sdunft.png">
      </div>
      <!--    用户名   -->
      <el-form-item prop="username" label="用户名:">
        <el-input v-model="registerForm.username" placeholder="请输入一个可用的用户名" ></el-input>
      </el-form-item>
      <!--    邮箱   -->
      <el-form-item prop="email" label="邮箱:">
        <el-input v-model="registerForm.email" placeholder="请输入验证邮箱"></el-input>
      </el-form-item>
      <!--    邮箱验证   -->
      <el-form-item prop="captcha" label="验证码:">
        <el-input v-model="registerForm.captcha" :disabled="codeRight" style="width: 50%" placeholder="请输入验证码"></el-input>
        <el-button   :type="valBunType" v-preventReClick style="position: absolute;right: 0; width: 40%;top: 0"
                      @click="sendEmailCode('registerForm',$event)" >发送验证码</el-button>
      </el-form-item>
      <!--    密码   -->
      <el-form-item prop="password" label="密码:">
        <el-input v-model="registerForm.password" type="password" placeholder="请输入密码" show-password></el-input>
      </el-form-item>
      <!--    确认密码   -->
      <el-form-item prop="comparePassword" label="确认密码:">
        <el-input v-model="registerForm.comparePassword" type="password" placeholder="确认密码" show-password ></el-input>
      </el-form-item>
      <el-button  class="input" type="info" style="margin-top: 20px;width: 80%;margin-left: 20%" native-type="submit" :loading="loading" @click="register" >注册</el-button>
      <div class="register-info"><el-button :to="{name: 'login'}" type="text" @click="gotoLogin" style="color: #409EFF;font-size: 16px;cursor:hand">
        已有账号，直接登录></el-button></div>
    </el-form>
    </div>
    </div>
  </div>
</template>

<script>
// eslint-disable-next-line no-unused-vars

export default {
  data: function () {
    return {
      loading: false,
      // 提示邮件已发送
      centerDialogVisible: false,
      //临时存放发送验证码按钮，方便后面清除按钮上倒计时的定时器
      codeButtonTemp:null,
      valDisable:false,
      valBunType:"primary",
      error: '',
      captcha:'',
      codeRight:true,
      registerForm: {
        username: '11111',
        email: '1330220320@qq.com',
        password: '111111',
        comparePassword: '111111',
        captcha:''
      },
      registerRules: {
        username: [
          {
            type: 'string',
            required: true,
            message: '用户名不能为空',
            trigger: 'blur'
          },
          {
            min: 4,
            max: 12,
            message: '用户名长度在4到12个字符之间'
          }],
        email: {
          type: 'email',
          required: true,
          message: '请输入有效的邮箱地址',
          trigger: 'blur'
        },
        captcha:{
          validator: function (rule, value, callback) {
            if (value === '') {
              callback(new Error('请输入验证码'))
            } else {
              callback()
            }
          }.bind(this)
        },
        password: [
          {
            type: 'string',
            required: true,
            message: '密码不能为空',
            trigger: 'blur'
          },
          {
            min: 4,
            max: 17,
            message: '密码长度在4到17个字符之间'
          }],
        comparePassword: {
          type: 'string',
          required: true,
          trigger: 'blur',
          validator: function (rule, value, callback) {
            console.log(this.registerForm.password)
            if (value === '') {
              callback(new Error('请再次输入密码'))
            } else if (value !== this.registerForm.password) {
              callback(new Error('两次输入的密码不一致'))
            } else {
              callback()
            }
          }.bind(this)
        }
      }
    }
  },
  methods: {
    // 点击按钮注册
    register: function () {
      // 创建json文件
      const json = {
        user_id: this.registerForm.username,
        password: this.registerForm.password,
        email_address: this.registerForm.email,
        email_code:this.registerForm.captcha
      };

      this.$refs.registerForm.validate(async valid => {
        // 预校验通过，发起请求
        if (valid) {
          const res = await this.$axios({
            url: '/verify',
            method:  'post',
            data: json
          })
          console.log(res)
          // 判断可否注册
          if (res.data.msg === "验证成功") {
            return this.$message.success('注册成功!')
          } else if (res.data.msg === "验证码输入错误") {
            return this.$message.error('验证码输入错误!')
          }
        } else {
          return this.$message.error('格式错误')
        }
      }
      )
    },
    // 返回登录页面
    gotoLogin() {
      this.$router.push('/login')
    },
    //发送邮箱验证码
    sendEmailCode(formName, event) {
      this.codeButtonTemp = event.currentTarget;
      console.log(this.codeButtonTemp)
      let va = true;
      this.$refs[formName].validateField('email', (valid) => {
        va = valid === "";
      })
      this.$refs[formName].validateField('username', (valid) => {
        va = va && valid === "";
      })
      if (va) {
        const json = {
          email_address: this.registerForm.email,
          user_id: this.registerForm.username
        };
      this.$axios({url: '/register', method: 'post', data: json}).then((res) => {
        console.log(res.status)
        if (res.status === 200) {
            this.codeRight = false;
            this.$countDown.setItem(this.codeButtonTemp);
            if(res.data.msg === "注册账号已存在")
              this.$message.warning("注册账号已存在");
            else if(res.data.msg === "注册邮箱已存在")
              this.$message.warning("注册邮箱已存在");
            else
              this.$message.success("验证码发送成功");
          } else if (res.status === 500) {
            this.$message.warning(res.data.message)
          }
        }).catch((err) => {
          this.$message.error("验证码发送失败，请稍后重试！")
        })
      }
    },
  }
}
</script>

<style lang="less" scoped>
// 容器
.register-container {
  background-image: url('../assets/image/register.jpg');
  background-size: 100% 100%;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
}
.register_in_title{
  font-size:35px;
  position: center;
  margin-top: 6%;
  padding-left: 10%;
}
.btn-orange{
  margin-left: 10%;
}
// 表单
.register-form {
  position: absolute;
  width: 35%;
  margin-left: 8%;
  margin-top: 5%;
  background: #0a1123;
  padding: 20px;
  box-sizing: border-box;
  .register-info {
    text-align: right;
    font-size: 0.9rem;
    margin-top: 10px;
    color: #0a1735
  }
  .register-error {
    color: #0a1735
  }
}
</style>
