<template>
  <div class="login_container">
    <img src="../assets/image/login.jpeg">
    <div class="login_box">
      <!-- 登陆界面 -->
      <el-form ref="loginFormRef" :model="User" :rules="loginFormRules"  class="login_form" :label-position="labelPosition">
        <el-form-item   >
          <div>
            <img class="login_in_title" src="../assets/image/sdunft.png">
          </div>
        </el-form-item>
        <!--用户名-->
        <el-form-item  prop="username" label="用户名/邮箱">
          <el-input v-model="User.username" class="input" placeholder="请输入用户名或邮箱"></el-input>
        </el-form-item>
        <!--密码-->
        <el-form-item  prop="password" label="密码">
          <el-input placeholder="请输入密码" v-model="User.password" class="input" show-password></el-input>
        </el-form-item>
        <!--按钮-->
        <el-form-item  >
          <el-button  class="input" type="info" style="margin-top: 30px" @click="login" >登录</el-button>
        </el-form-item>
        <div class="registerButton" style="color: whitesmoke;padding-left: 63%;padding-top: 10px">
          没有账号?<el-button type="text" @click="gotoRegister" style="font-size:18px">注册</el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      labelPosition:'top',
      User: {
        username: '111',
        password: '111'
      },
      loginFormRules: {
        username: [
          { required: true, message: '请输入用户名或邮箱', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 1, max: 16, message: '长度在 8 到 16 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    login () {
      const json = {
        user_id: this.User.username,
        password: this.User.password
      };

      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return this.$message.error('格式错误')
        // 发送post请求
        const res = await this.$axios({
          url: '/login',
          method:  'post',
          data: json
        });
        console.log(res)
        if (res.data.msg === "账号不存在" || res.data.msg==="密码错误"  ) return this.$message.error('账户或密码错误')
        this.$message.success('登陆成功')
        // 1. 登录成功之后将token保存在客户端的 sessionStorage
        //  1.1项目中除了登陆之外的其他API接口，必须在登陆之后才能访问
        //  1.2token只应在当前网站打开期间生效，多以将token保存在sessionStorage
        // 2.通过编程式导航导入后端，路由地址是‘/home’
        if(res.data.msg === "用户登录成功")
          await this.$router.push('/home')
        else if(res.data.msg === "管理员登录成功")
          await this.$router.push('/managerCenter')
      })
    },
    gotoRegister () {
      this.$router.push('/register')
    }
  }
}
</script>

<style lang="less" scoped>

/deep/.el-input__suffix{
  color:#111326
}

.login_container{
  height: 100%;
  background-color: #111326;
  img {
    width: 100%;
    height: 100%;
  }
}
.login_in_title{
  color: white;
  font-size:35px;
  position: center;
  padding-left: 8%;
  padding-top: 50%;
}
.login_box {
  width: 300px;
  height: 300px;
  background-color: #111326;
  position: absolute;
  left: 30%;
  top: 60%;
  transform: translate(-50%, -50%);

    .login_form {
    position: absolute;
    bottom: 0;
    width: 100%;
    box-sizing: border-box;
  }
  .input{
    width: 350px;
  }

  .loginButton {
    display: flex;
    justify-content: flex-end;
  }
  .registerButton {
    padding-left: 150px;
  }
}
</style>
