<template>
  <div class="market" >
    <!--    回到顶部-->
    <el-backtop :bottom="100">
      <div class="top">Top</div>
    </el-backtop>
    <!--    布局容器-->
    <el-container class="marketContainer">
      <!--      头部区域-->
      <el-header class="el-header">
        <!--        标题-->
        <div class="market_img">
          <img src="../assets/image/change.jpg" style="display: inline-block;width: 100px;margin-top:20px">
        </div>
        <!--        搜索框-->
        <div class="searchInput">
          <el-input v-model="queryInfo.keyword" prefix-icon="el-icon-search" placeholder="请输入要搜索的商品" clearable>
            <el-button slot="append" icon="el-icon-search" @click="searchItem()" >搜索</el-button>
          </el-input>
        </div>
        <!--        按钮区域-->
        <span style="display:inline-block;width:270px;font-weight:bold" class="tree_head_span_search">
           <el-button type="text" @click="pathTo('/home')">返回主页</el-button>
           <el-button type="text" @click="pathTo('/personal_information')">个人中心</el-button>
           <el-button type="text">账号退出</el-button>
        </span>
      </el-header>
      <!--      页面主体区域-->
      <el-main class="el-main">

        <!--        类型选择-->
        <div class="firstCard">
          <div>
            <div class="allCategories">所有分类></div>
          </div>
          <!--          分类卡片区域-->
          <el-card class="box-card-sort">
            <!--            范围限制-->
            <div>
              <a>范围 ：</a>
              <el-radio-group v-model="range" @change="rangeChange">
                <el-radio  label="1">本校</el-radio>
                <el-radio  label="0">全部</el-radio>
              </el-radio-group>
            </div>
            <!--            水平分界线-->
            <el-divider></el-divider>
            <!--            类别选择-->
            <div>
              <a>类别选择 : </a>
              <el-radio-group v-model="itemCategory"@change="typeChange">
                <el-radio :label="''">全部</el-radio>
                <el-radio :label="'图书'">图书</el-radio>
                <el-radio :label="'零食'">零食</el-radio>
                <el-radio :label="'衣服'">衣服</el-radio>
                <el-radio :label="'电子产品'">电子产品</el-radio>
                <el-radio :label="'其它'">其它</el-radio>
              </el-radio-group>
            </div>
          </el-card>
        </div>
        <!--        商品主体-->
        <div class="tree_main"  v-for="(item, index) in postInfo">
          <!--          每件商品的卡片-->
          <el-card class="box-card-commodity">
            <!--            第一层信息，显示为卖主信息-->
            <div>
              <!--          头像-->
              <el-avatar :src="avatarSrc[index]"></el-avatar>
              <!--          用户名-->
              <span>{{item.username+' '}}</span>
              <!--          学校-->
              <span>{{item.school+' '}}</span>
              <!--          时间-->
              <span>{{'消失时间： '+item.die_time+'   '}}</span>
            </div>
            <!--          水平分割线-->
            <hr>
            <!--            第二层信息显示为商品信息+联系方式-->
            <div class="secondAspect">
              <!--              商品图片-->
              <span class="picture_item" style="display: block;width: 50%" float="left">
                <el-image
                    style="width: 150px; height: 100px"
                    :src="itemPicture[index]"
                    :fit="fits"
                    class="itemPicture">
                </el-image>
              </span>
              <!--              文字信息-->
              <span class="information_item" style="display: block;width: 80%" float="right"
              >
                <el-card class="itemInformationCard">
                  <div class="itemInformation">
                    <span>{{'商品类别 ： '+item.type+' '}}</span>
                    <br>
                    <span>{{'商品名称 ： '+item.thing_name+' '}}</span>
                    <br>
                    <span>{{'商品价格 ： '+item.price+'¥'}}</span>
                    <br>
                    <span>{{'联系方式： '+item.phone+' '}}</span>
                    <br>
                    <span>{{'QQ ： '+item.qq+' '}}</span>
                    <br>
                    <span>{{'备注 ： '+item.remark+' '}}</span>
                  </div>
                </el-card>
              </span>
            </div>
            <!--&lt;!&ndash;            点击按钮显示备注&ndash;&gt;-->
<!--                        <el-button type="text" icon="el-icon-s-comment"-->
<!--                                   round @click="showRemark(index)" class="remarkButton">备注<</el-button>-->
          </el-card>
          <!--              备注区，根据showRemarks数组切换是否可见-->
<!--                    <div  v-if="showRemarks[index]" :key="index" class="box-card-sort">-->
<!--                        <el-input v-model="item.remark" readonly type="textarea" autosize>-->
<!--                        </el-input>-->
<!--                    </div>-->
        </div>
      </el-main>
      <!--      底部区域-->
      <el-footer class="el-footer">
        <!--        递交二手物品-->
        <el-button class="postBtn" @click="postPage">我要发贴</el-button>
        <!--            分页栏，分页数据与queryInfo相关数据绑定-->
          <el-pagination
              @current-change="handleCurrentChange"
              :current-page.sync="queryInfo.pageNum"
              :page-sizes="[10]"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
        </el-pagination>
      </el-footer>
    </el-container>
    <!--    发帖对话框-->
    <el-dialog
        title="商品添加"
        :visible.sync="postDialogVisible"
        width="35%"
        @close="addItemDialogClosed">
      <!--      帖子信息区域-->
      <!--      商品表单-->
      <el-form :model="addItemForm" :rules="addItemFormRule" ref="addItemFormRef" label-width="70px" class="addItemForm">
        <!--        商家用户名-->
        <el-form-item label="用户名" prop="username">
          <el-input v-model="addItemForm.username" readonly></el-input>
        </el-form-item>
        <!--   商品类别选择  -->
        <el-form-item label="物品类别">
          <el-select v-model="addItemForm.itemType" placeholder="请选择物品类别">
            <el-option label="图书" value="图书"></el-option>
            <el-option label="零食" value="零食"></el-option>
            <el-option label="衣服" value="衣服"></el-option>
            <el-option label="电子产品" value="电子产品"></el-option>
            <el-option label="其他" value="其他"></el-option>
          </el-select>
        </el-form-item>
        <!--        商品名-->
        <el-form-item label="物品名" prop="itemName">
          <el-input v-model="addItemForm.itemName" ></el-input>
        </el-form-item>
        <!--        价格-->
        <el-form-item label="价格" prop="price">
          <el-input v-model="addItemForm.price" oninput="value=value.replace(/[^\d]/g, '')"></el-input>
        </el-form-item>
        <!--        联系电话-->
        <el-form-item label="联系电话" prop="phone">
          <el-input v-model="addItemForm.phone" ></el-input>
        </el-form-item>
        <!--        qq-->
        <el-form-item label="QQ" prop="qq">
          <el-input v-model="addItemForm.qq" ></el-input>
        </el-form-item>
        <!--        备注-->
        <el-form-item label="备注">
          <el-input type="textarea" v-model="addItemForm.remarks"
                    maxlength="120"  show-word-limit  autosize></el-input>
        </el-form-item>
        <!--        上传图片使用-->
        <el-upload
            class="upload-demo"
            ref="upload"
            action=""
            :http-request="uploadImg"
            :on-remove="handleRemove"
            :on-exceed="handleExceed"
            :on-success="successUpload"
            :file-list="fileList"
            :auto-upload="false"
            :limit="1">
          <el-button slot="trigger" size="small" type="primary">选取图片</el-button>
          <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>
      </el-form>
      <!--      按钮区域-->
      <span slot="footer" class="dialog-footer">
        <el-button @click="postDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="postItem">发 表</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    // 验证手机号的规则
    let checkMobile = (rule,value,cb)=>{
      const regMobile = /^1(3|4|5|6|7|8|9)\d{9}$/
      if (regMobile.test(value) || value === ''){
        //  合法的手机号
        return cb()
      }
      cb(new Error('请输入正确的手机号'))
    }
    // 验证QQ号的规则
    let checkQQNumber = (rule,value,cb)=>{
      const regQQ =  /^[0-9]{5,12}$/
      if (regQQ.test(value) || value === ''){
        //  合法的QQ号
        return cb()
      }
      cb(new Error('请输入正确的QQ号'))
    }
    return {
      // 用户头像
      imgUrl: '',
      // 取得的商品数
      total: 1,
      // 范围选择， 1仅为本校，0表示全部
      range: '1',
      // 类别选择,是否全选
      selectAll: true,
      // 已选物品类别
      itemCategory: '',
      loading: false,
      // 商品关键字，页数，一页个数
      queryInfo: {
        keyword: '',
        pageNum: 1,
        pageSize: 10
      },
      // 商品卖家头像
      avatarSrc:[
      ],
      // 商品图片基础信息
      fits: ['fill', 'contain', 'cover', 'none', 'scale-down'],
      // 备注显示
      // showRemarks: [false, false, false, false, false, false, false, false, false, false],
      // 商品其他信息
      postInfo:[
      ],
      // 学校
      school:[
      ],
      // 添加商品表单
      addItemForm:{
        username :'',
        itemName :'',
        itemType :'',
        school :'',
        campus :'',
        phone :'',
        qq :'',
        price :'',
        time: '',
        remarks: '',
      },
      // 该用户信息数组
      studentForm: [],
      // 添加商品规则
      addItemFormRule:{
        //用户名规则
        itemName: {
          required: true,
          trigger: 'blur',
          min: 2,
          max: 12,
          message: '商品名称长度在2到12个字符之间'
        },
        //备注规则
        remarks: {
          required: true,
          trigger: 'blur',
          message: '备注不可为空'
        },
        // 价格规则
        price: {
          required: true,
          message: '请输入正确的价格',
          trigger: 'blur'
        },
        phone: {
          required: false,
          validator: checkMobile,
          trigger: "blur"
        }
        ,
        qq: {
          validator: checkQQNumber,
          trigger: 'blur'
        },
      },
      //上传图片绑定列表
      fileList: [],
      // 商品图片
      itemPicture:[
      ],
      // 判断发帖窗口要不要打开
      postDialogVisible: false,
    }
  },
  // 生命周期函数
  async created(){
    // 得到username 和 token
    this.username = window.localStorage.getItem('username')
    const token = window.sessionStorage.getItem('token')
    // 获取学生用户信息
    await this.getStudentInformation()
    // 获得商品属性
    await this.getItemMes()
    // 监听bool数组元素改变
    this.$forceUpdate()
  },
  // 函数
  methods: {
    //页面切换
    pathTo(path){
      this.$router.push({
        path: path
      })
    },
    //根据商品id获取商品图片并赋值给对应的变量!!!!!需要改变
    async getItemPicture(itemNumber, index) {
      const token = window.sessionStorage.getItem('token')
      const formData = new FormData()
      formData.append('itemNumber', itemNumber)
      // 与后端交互
      var that = this
      const imgData = await this.$axios.get('/getImage_thing',
          {
            params :{
              'thing_id': itemNumber
            },
            responseType: 'arraybuffer',
            headers: {
              'accesstoken': token
            }
          }
      ).then(response => {
        console.log(response)
        return (
            "data:image/png;base64," +
            btoa(
                new Uint8Array(response.data).reduce(
                    (data, byte) => data + String.fromCharCode(byte),
                    ""
                )
            )
        );
      })
          .then(data => {
            that.itemPicture[index] = data; //赋值给img标签的src属性
            console.log(this.itemPicture)
          })
    },
    //根据用户名获取头像并赋值给对应的变量
    async getUserMes(name) {
      let result
      const token = window.sessionStorage.getItem('token')
      const formData = new FormData()
      formData.append('username', name)
      const imgData = await this.$axios.get('/getImage_user',
          {
            responseType: 'arraybuffer',
            headers: {
              'accesstoken': token
            },
            params: {
              username: name
            }
          }
      ).then(response => {
        return (
            "data:image/png;base64," +
            btoa(
                new Uint8Array(response.data).reduce(
                    (data, byte) => data + String.fromCharCode(byte),
                    ""
                )
            )
        );
      })
          .then(data => {
            result = data
          })
      return result
    },
    // 根据用户名获取数据
    async getUserSchool(name) {
      let result
      const token = window.sessionStorage.getItem('token')
      const formData = new FormData()
      formData.append('username', name)
      const { data: res} = await this.$axios.get('/get_mess2', {
            headers: {
              'accesstoken': token
            },params:{
              'username' :name
        }
          }
      )
      if(!res){
        this.$message.error('获取信息失败')
      }
      return res.school
    },
    // 根据queryInfo获得帖子列表的函数,获得商品的各个属性（待写） !!!!!
    async getItemMes() {
      // 页面进入待刷新状态
      this.loading = true
      console.log("111")
      // 传递查询参数
      const formData = new FormData()
      formData.append('pagenum', this.queryInfo.pageNum)
      formData.append('keywords', this.queryInfo.keyword)
      formData.append('type', this.itemCategory)
      formData.append('inschool', this.range)

      const token = window.sessionStorage.getItem('token')

      const { data: res} = await this.$axios.post('/thing_get', formData,
          {
            headers: {
              'accesstoken': token
            }
          })
      if(!res){
        this.$message.error('获取信息失败')
      }
      // 得到商品数据
      this.postInfo=res
      console.log(res.length)
      console.log(this.postInfo)
      // 商品个数
      this.total = res.length
      //逐个获得商品照片
      let j = 0;
      for( ;j < res.length; j++){
        let username = this.postInfo[j].username
        // // 获得商品图片
        await this.getItemPicture(this.postInfo[j].thing_id, j)
        // 获得用户图片
        this.avatarSrc[j] = await this.getUserMes(username)
      }
      this.$forceUpdate()
      // 页面刷新成功
      this.loading = false
      this.$message.success('商品信息刷新成功')
    },
    // 获取学生信息(有待验证) !!!!!
    async getStudentInformation(){
      console.log("!!!!!!!!!!!!!!!")
      const token = window.sessionStorage.getItem('token')
      const data = await this.$axios.get('/get_mess',
          {
            headers: {
              'accesstoken': token
            }
          }
      )
      console.log("!!!!!!!!!!!!!!!")
      console.log(data.data)
      if(data.data.username === '')
        return this.$message.error('获取学生信息失败!')
      else {
        // 赋值
        this.studentForm = data.data
        this.addItemForm.phone = data.data.phone;
        this.addItemForm.qq = data.data.qq;
        this.addItemForm.username = data.data.username;
        console.log(this.addItemForm.qq)
      }
    },
    //搜索内容,并刷新商品,并且改变类别
    async searchItem(){
      // 刷新商品
      await this.getItemMes()
    },
    //  范围改变,改变查找范围
    async rangeChange(){
      console.log(this.range)
      await this.getItemMes()
    },
    // 改变类别
    async typeChange(){
      console.log(this.itemCategory)
      await this.getItemMes()
    },
    // 发帖窗口打开
    postPage(){
      this.postDialogVisible = true
    },
    // 上传新商品,仍待完善
    async postItem(){
      await this.$refs.upload.submit()
    },
    // 关闭添加商品对话框
    addItemDialogClosed(){
      this.$refs.addItemFormRef.resetFields()
      this.addItemForm.remarks = ''
    },
    // 监听页码值改变的事件
    async handleCurrentChange (newPage) {
      this.queryInfo.pageNum = newPage
      await this.getItemMes()
    },
    // 以下两个函数作用为对el-upload的某些函数
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handleExceed() {
      this.$message.error('上传图片数超过限制')
    },
    // 成功上传商品图片提示
    successUpload() {
      console.log('success')
    },
    // 上传商品图片(需要改) !!!!!
    async uploadImg(param){
      this.$refs.addItemFormRef.validate(async valid =>{
        if(!valid) return
        //  发起修改用户信息请求
        // 传递查询参数
        const token = window.sessionStorage.getItem('token')
        const addItemForm1 = new FormData()
        addItemForm1.append('thing_name', this.addItemForm.itemName)
        addItemForm1.append('type', this.addItemForm.itemType)
        addItemForm1.append('price', this.addItemForm.price)
        addItemForm1.append('phone', this.addItemForm.phone)
        addItemForm1.append('qq', this.addItemForm.qq)
        addItemForm1.append('remark', this.addItemForm.remarks)
        addItemForm1.append('multipartFile', param.file)
        const {data:res} = await this.$axios.post('/thing_insert',addItemForm1,{
          header: {
            'accesstoken': token,
            'Content-Type': 'multipart/form-data'
          }
        })
        console.log(res)
        if(!res) {
          return this.$message.error('商品上传失败')
        }
        //  关闭对话框，刷新列表，提示修改成功
        this.postDialogVisible = false
        await this.getItemMes()
        this.$message.success('商品上传成功!')
      })
    },
    // 调用上传图片的函数
    async postImg(param) {
      await this.$refs.upload.submit();
    },
    // 显示每一项的备注
    // showRemark(i) {
    //   this.showRemarks[i] = !this.showRemarks[i];
    //   console.log(this.showRemarks)
    //   this.$forceUpdate();
    // },
  },
  name: "market"
}
</script>

<style land="less" scoped>
