  background-color: #f5f5f5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 150px !important;
}
/*图标区域*/
.market_img {
  display: inline-block;
  width: 100px;
  height: 100%;
  margin-left: 200px;
}
/*搜索输入框*/
.searchInput {
  display: inline-block;
  float: left;
  margin-top: 4px;
  height: 35px;
  width: 35%;
  padding-bottom: 5px;
  margin-left: 10px;
}
/*个人中心*/
.tree_head_span_search {
  float: top;
  width:270px;
  font-weight:bold;
  padding-bottom: 90px;
}
/* 主体区域*/
.el-main {
  padding-left: 250px;
  background-color: #f5f5f5;
}
/*主容器*/
.marketContainer{
  display: flex;
  flex-direction: column;
  min-height: 100%;
  height: 100%;
}
/* 所有分类文字框*/
.allCategories{
  padding-bottom: 10px;
}
/*第一个卡片*/
.firstCard{
  padding-bottom: 35px;
}
/*分类选择卡片*/
.box-card-sort{
  width: 80%;
  padding-bottom: 20px;
}
/*商品卡片*/
.box-card-commodity{
  width: 80%;
  padding-bottom: 20px;
}
