  width: 40%;
}
.itemInformation{
  width: 40%
}
.searchInput2{
  margin-top: 20px;
  float: top;
  height: 35px;
  width: 35%;
  padding-bottom: 15px;
  padding-left: 300px;
}

/* 底部区域*/
.el-footer {
  background-color: #f5f5f5;
  display: flex;
  justify-content: space-between;
  align-items: center;
